.lhs__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 6rem;
}

.lhs__navbar-links {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.lhs__navbar-links_logo {
    margin-right: 2rem;
}

.lhs__navbar-links_logo img {
    width: 121.33px;
    height: 16.5px;
}

.lhs__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.lhs__navbar-vevnt {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.lhs__navbar-links_container p,
.lhs__navbar-vevnt p,
.lhs__navbar-menu_container p {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;

    margin: 0 1rem;
    cursor: pointer;
}

.lhs__navbar-vevnt button,
.lhs__navbar-menu_container button {
padding: 0.5rem 1rem;
color: #fff;
font-family: var(--font-family);
font-weight: 500;
font-size: 18px;
line-height: 25px;
border: none;
outline: none;
cursor: pointer;

background: #7D938A;
border-radius: 5px;
}

.lhs__navbar-menu {
    margin-left: 1rem;
    display:none;
    position: relative;
}

.lhs__navbar-menu svg {
    cursor: pointer;
}

.lhs__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(0,0,0,0.2);
}

.lhs__navbar-menu_container p {
    margin: 1rem 0;
}

.lhs__navbar-menu_container-links-vevnt {
    display: none;
}

@media screen and (max-width: 1050px) {
    .lhs__navbar-links_container {
        display: none;
    }
    .lhs__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 700px) {
    .lhs__navbar {
        padding: 2rem 4rem;
    }
}

@media screen and (max-width: 550px) {
    .lhs__navbar {
        padding: 2rem;
    }
    .lhs__navbar-vevnt {
        display: none;
    }
    .lhs__navbar-menu_container {
        top: 20px;
    }
    .lhs__navbar-menu_container-links-vevnt{
        display: block;
    }
}